    "probability.header": " Olaslk Yzdesi",
    "win.percentage":
      "Kazanma Yzdesi hesaplanma mant; Orann kazanma ve kaybetme saylarn kullanarak yaplan hesaplama ile orann %(yzdelik) olarak kazanma performansn bize vermektedir.{br}{br}Bu hesaplamay bir rnek ile aklayalm;{br}{br}Ma sonucu 1 iin 2.75 orann ele alalm. MS1 - 2,75 oran u ana kadar 22 defa verilmi olsun 5 defa kazanm ve 17 defa kaybettiini dnelim.{br}{br}Kazanan ma says: 5{br}{br}Kaybeden ma says: 17{br}{br}Kazanma % : (5 / 22) * 100 = %22.73{br}{br}Kazanma Yzdesi hesaplamas ile ilgili orann kazanma ans: %22.73 olarak kmaktadr.",
    "profit.loose":
      "Kar-Zarar Yzdesi hesaplanma mant;Orann kazanma says, kaybetme says ve orann kendisi kullanlarak yaplan hesaplama ile orann %(yzdelik) olarak Kar-Zarar performansn bize vermektedir.{br}{br}Kar-Zarar Yzdesi hesapkamasn bir rnek ile aklayalm;{br}{br}Ma sonucu 1 iin 2.75 orann ele alalm. MS1 - 2,75 oran u ana kadar 22 defa iddaa tarafndan verilmi olsun 5 defa kazanm ve 17 defa kaybettiini dnelim.{br}{br}Kazanan ma says: 5{br}{br}Kaybeden ma says: 17{br}{br}Toplam Bahis Tutar: 1 x 22 = 22 TL{br}{br}Kazandmz Tutar: 2.75 x 5 = 13,75 TL{br}{br}Toplam Bakiye : 13,75 - 22 = - 8,25 TL{br}{br}VBET: ((13,75  22) / 22) * 100 = % - 37.5{br}{br}lgili orann Kar-Zarar hesaplamas sonucunda 8,25 TL kaybetmi oldu. Bu hesaba gre Kar-Zarar yzdesi % - 37.5 olarak grnmektedir.",
    probability:
      "Orann kendi bahis grubu ierisindeki kazanma ansn vermektedir.{br}{br}Bu hesaplamay bir rnekle anlatmak gerekirse.{br}{br}Ma sonucu bahis grubunu ele alalm. Ma Sonu bahis grubunda MS1 (Ma Sonu 1), MS0 (Ma Sonu 0) ve MS2 (Ma Sonu 2) olarak 3 farkl bahis tr bulunmaktadr.{br}{br}MS1: 2.00 MS0: 3.00 MS2: 2.70 olarak ele alalm.{br}{br}KAR hesaplamas : (1/M1) + (1/M0) + (1/M2) toplam KAR payn vermektedir. Bu oran ne kadar dk olursa iddaann kar oran da o kadar dk olur. Bu orann dk olduu malara oynamak aslnda bahisiler iin daha fazla kazanma ans getirdii de sylenmektedir.{br}{br}KAR PAYI: (1/M1) + (1/M0) + (1/M2){br}{br}yukardaki hesaplamaya gre 1 TL iin datlacak toplam tutar ise;{br}{br}DAITILACAK TUTAR: 1/ KAR PAYI{br}{br}DAITILACAK TUTAR ORANI: 100 * DAITILACAK TUTAR{br}{br}imdide oranlarn yzdelik dilimlerini hesaplayalm.{br}{br}M1: 1 / (DAITILACAK TUTAR ORANI * M1){br}{br}M0: 1 / (DAITILACAK TUTAR ORANI * M0){br}{br}M2: 1 / (DAITILACAK TUTAR ORANI * M2){br}{br}    imdi bu hesaplamamz yukardaki oranlarla hesaplayalm.{br}{br}KAR PAYI: (1/2.00) + (1/3.00) + (1/2.70) = 0,50 + 0,33 + 0,37 = 1,20{br}{br}DAITILACAK TUTAR: 1/1.20 = 0,83{br}{br}DAITILACAK TUTAR ORANI: 0,83 * 100 = 83{br}{br}(yani 1 TL iin 0,83 TL para datlmas beklenmektedir. Bu oran grubunda kar pay  0,17 TL dir.){br}{br}M1: (1 / 2.00) * 0,83 = 0,415 * 100 = % 41,5{br}{br}    M0: (1 / 3.00) * 0,83 = 0,276 * 100 = % 27,6{br}{br}M2: (1 / 2.70) * 0,83 = 0,307 * 100 = % 30,7{br}{br} Hesaplamalar sonucunda her bir orann Kazanma Olaslk Yzdesini bulmu olduk..{br}{br}",
